:root {
  --font-default: Open Sans, system-ui, -apple-system, Segoe UI, Roboto, Helvetica Neue, Arial, Noto Sans, Liberation Sans, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji;
  --font-primary: Inter, sans-serif;
  --font-secondary: Poppins, sans-serif;
  --color-default: #0a0d13;
  --color-primary: white;
  --color-secondary: #0e1d34;
  scroll-behavior: smooth;
}

body {
  font-family: var(--font-default);
  color: var(--color-default);
}

a {
  color: var(--color-primary);
  text-decoration: none;
}

a:hover {
  color: #406aff;
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6 {
  font-family: var(--font-primary);
}

section {
  padding: 80px 0;
  overflow: hidden;
}

.section-header {
  text-align: center;
  padding: 30px 0;
  position: relative;
}

.section-header h2 {
  font-size: xxx-large;
  text-transform: uppercase;
  color: #fff;
  z-index: 2;
  margin-bottom: 20px;
  padding-bottom: 20px;
  font-weight: 700;
  position: relative;
}

.section-header h2:after {
  content: "";
  width: 50px;
  height: 3px;
  background: var(--color-primary);
  margin: auto;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.section-header span {
  color: #0e1d340f;
  z-index: 1;
  text-transform: uppercase;
  font-size: 56px;
  font-weight: 700;
  line-height: 0;
  position: absolute;
  top: 46px;
  left: 0;
  right: 0;
}

.section-header p {
  color: #fff;
  z-index: 2;
  margin-bottom: 0;
  position: relative;
}

@media (max-width: 640px) {
  .section-header h2 {
    margin-bottom: 15px;
    font-size: 28px;
  }

  .section-header span {
    font-size: 38px;
  }
}

.breadcrumbs .page-header {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 140px 0 80px;
  position: relative;
}

.breadcrumbs .page-header:before {
  content: "";
  background-color: #0e1d34cc;
  position: absolute;
  inset: 0;
}

.breadcrumbs .page-header h2 {
  color: #fff;
  font-size: 56px;
  font-weight: 500;
  font-family: var(--font-secondary);
}

.breadcrumbs .page-header p {
  color: #fffc;
}

.breadcrumbs nav {
  background-color: #f3f6fc;
  padding: 20px 0;
}

.breadcrumbs nav ol {
  color: var(--color-secondary);
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-weight: 600;
  list-style: none;
  display: flex;
}

.breadcrumbs nav ol a {
  color: var(--color-primary);
  transition: all .3s;
}

.breadcrumbs nav ol a:hover {
  text-decoration: underline;
}

.breadcrumbs nav ol li + li {
  padding-left: 10px;
}

.breadcrumbs nav ol li + li:before {
  color: #4278cc;
  content: "/";
  padding-right: 10px;
  display: inline-block;
}

.scroll-top {
  visibility: hidden;
  opacity: 0;
  z-index: 99999;
  background: var(--color-primary);
  width: 40px;
  height: 40px;
  border-radius: 4px;
  transition: all .4s;
  position: fixed;
  bottom: 15px;
  right: 15px;
}

.scroll-top i {
  color: #fff;
  font-size: 24px;
  line-height: 0;
}

.scroll-top:hover {
  color: #fff;
  background: #0d42ffcc;
}

.scroll-top.active {
  visibility: visible;
  opacity: 1;
}

#preloader {
  z-index: 9999;
  width: 100%;
  height: 100vh;
  background: #fff;
  transition: all .6s ease-out;
  position: fixed;
  inset: 0;
  overflow: hidden;
}

#preloader:before, #preloader:after {
  content: "";
  border: 4px solid var(--color-primary);
  border-radius: 50%;
  animation: animate-preloader 2s cubic-bezier(0, .2, .8, 1) infinite;
  position: absolute;
}

#preloader:after {
  animation-delay: -.5s;
}

@keyframes animate-preloader {
  0% {
    width: 10px;
    height: 10px;
    opacity: 1;
    top: calc(50% - 5px);
    left: calc(50% - 5px);
  }

  100% {
    width: 72px;
    height: 72px;
    opacity: 0;
    top: calc(50% - 36px);
    left: calc(50% - 36px);
  }
}

@media screen and (max-width: 768px) {
  [data-aos-delay] {
    transition-delay: 0 !important;
  }
}

.header {
  z-index: 997;
  padding: 15px 0;
}

.header.sticked {
  background: #000;
  padding: 15px 0;
}

.header .logo img {
  max-height: 60px;
  margin-right: 6px;
}

.header .logo h1 {
