@media (min-width: 1280px) {
  .navbar {
    padding: 0;
  }

  .navbar ul {
    align-items: center;
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
  }

  .navbar li {
    position: relative;
  }

  .navbar a, .navbar a:focus {
    font-family: var(--font-primary);
    color: #fff9;
    white-space: nowrap;
    justify-content: space-between;
    align-items: center;
    padding: 10px 0 10px 30px;
    font-size: 20px;
    font-weight: 400;
    transition: all .3s;
    display: flex;
  }

  .navbar a i, .navbar a:focus i {
    margin-left: 5px;
    font-size: 12px;
    line-height: 0;
  }

  .navbar a:hover, .navbar .active, .navbar .active:focus, .navbar li:hover > a {
    color: #fff;
  }

  .navbar .get-a-quote, .navbar .get-a-quote:focus {
    background: var(--color-primary);
    color: #fff;
    border-radius: 4px;
    margin-left: 30px;
    padding: 8px 20px;
  }

  .navbar .get-a-quote:hover, .navbar .get-a-quote:focus:hover {
    color: #fff;
    background: #2756ff;
  }

  .navbar .dropdown ul {
    z-index: 99;
    opacity: 0;
    visibility: hidden;
    background: #fff;
    border-radius: 4px;
    margin: 0;
    padding: 10px 0;
    transition: all .3s;
    display: block;
    position: absolute;
    top: calc(100% + 30px);
    left: 14px;
    box-shadow: 0 0 30px #7f89a140;
  }

  .navbar .dropdown ul li {
    min-width: 200px;
  }

  .navbar .dropdown ul a {
    text-transform: none;
    color: var(--color-secondary);
    padding: 10px 20px;
    font-size: 15px;
    font-weight: 400;
  }

  .navbar .dropdown ul a i {
    font-size: 12px;
  }

  .navbar .dropdown ul a:hover, .navbar .dropdown ul .active:hover, .navbar .dropdown ul li:hover > a {
    color: var(--color-primary);
  }

  .navbar .dropdown:hover > ul {
    opacity: 1;
    visibility: visible;
    top: 100%;
  }

  .navbar .dropdown .dropdown ul {
    visibility: hidden;
    top: 0;
    left: calc(100% - 30px);
  }

  .navbar .dropdown .dropdown:hover > ul {
    opacity: 1;
    visibility: visible;
    top: 0;
    left: 100%;
  }
}

@media (min-width: 1280px) and (max-width: 1366px) {
  .navbar .dropdown .dropdown ul {
    left: -90%;
  }

  .navbar .dropdown .dropdown:hover > ul {
    left: -100%;
  }
}

@media (min-width: 1280px) {
  .mobile-nav-show, .mobile-nav-hide {
    display: none;
  }
}

@media (max-width: 1279px) {
  .navbar {
    width: 100%;
    max-width: 400px;
    z-index: 9997;
    transition: all .3s;
    position: fixed;
    top: 0;
    bottom: 0;
    right: -100%;
  }

  .navbar ul {
    z-index: 9998;
    background: #0e1d34e6;
    margin: 0;
    padding: 50px 0 10px;
    transition: all .3s;
    position: absolute;
    inset: 0;
    overflow-y: auto;
  }

  .navbar a, .navbar a:focus {
    font-family: var(--font-primary);
    color: #ffffffb3;
    white-space: nowrap;
    justify-content: space-between;
    align-items: center;
    padding: 12px 20px;
    font-size: 16px;
    font-weight: 500;
    transition: all .3s;
    display: flex;
  }

  .navbar a i, .navbar a:focus i {
    margin-left: 5px;
    font-size: 12px;
    line-height: 0;
  }

  .navbar a:hover, .navbar .active, .navbar .active:focus, .navbar li:hover > a {
    color: #fff;
  }

  .navbar .get-a-quote, .navbar .get-a-quote:focus {
    background: var(--color-primary);
    color: #fff;
    border-radius: 4px;
    margin: 15px;
    padding: 8px 20px;
  }

  .navbar .get-a-quote:hover, .navbar .get-a-quote:focus:hover {
    color: #fff;
    background: #0d42ffcc;
  }

  .navbar .dropdown ul, .navbar .dropdown .dropdown ul {
    border: 1px solid #19335c;
    margin: 10px 20px;
    padding: 10px 0;
    transition: all .5s ease-in-out;
    display: none;
    position: static;
  }

  .navbar .dropdown > .dropdown-active, .navbar .dropdown .dropdown > .dropdown-active {
    display: block;
  }

  .mobile-nav-show {
    color: #fff;
    cursor: pointer;
    z-index: 9999;
    margin-right: 10px;
    font-size: 28px;
    line-height: 0;
    transition: all .5s;
  }

  .mobile-nav-hide {
    color: #fff;
    cursor: pointer;
    z-index: 9999;
    font-size: 32px;
    line-height: 0;
    transition: all .5s;
    position: fixed;
    top: 20px;
    right: 20px;
  }

  .mobile-nav-active {
    overflow: hidden;
  }

  .mobile-nav-active .navbar {
    right: 0;
  }

  .mobile-nav-active .navbar:before {
    content: "";
    z-index: 9996;
    background: #0e1d34cc;
    position: fixed;
    inset: 0;
  }
}

