.about .play-btn {
  width: 94px;
  height: 94px;
  background: radial-gradient(var(--color-primary) 50%, #0d42ff66 52%);
  border-radius: 50%;
  display: block;
  position: absolute;
  top: calc(50% - 47px);
  left: calc(50% - 47px);
  overflow: hidden;
}

.about .play-btn:before {
  content: "";
  width: 120px;
  height: 120px;
  animation: pulsate-btn 2s;
  -webkit-animation-direction: forwards;
  animation-direction: forwards;
  animation-iteration-count: infinite;
  -webkit-animation-timing-function: steps;
  animation-timing-function: steps;
  opacity: 1;
  background: #c6100000;
  border: 5px solid #0d42ffb3;
  border-radius: 50%;
  position: absolute;
  top: -15%;
  left: -15%;
}

.about .play-btn:after {
  content: "";
  width: 0;
  height: 0;
  z-index: 100;
  border-top: 10px solid #0000;
  border-bottom: 10px solid #0000;
  border-left: 15px solid #fff;
  transition: all .4s cubic-bezier(.55, .055, .675, .19);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-40%)translateY(-50%);
}

.about .play-btn:hover:before {
  content: "";
  width: 0;
  height: 0;
  z-index: 200;
  border: 10px solid #0000;
  border-left: 15px solid #fff;
  border-right: none;
  border-radius: 0;
  animation: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-40%)translateY(-50%);
}

.about .play-btn:hover:after {
  border-left: 15px solid var(--color-primary);
  transform: scale(20);
}

@keyframes pulsate-btn {
  0% {
    opacity: 1;
    transform: scale(.6);
  }

  100% {
    opacity: 0;
    transform: scale(1);
  }
}

